"use client";
import { motion } from "framer-motion";

export default function Hero() {
  return (
    <section className="relative h-screen bg-gray-500 flex items-center justify-center px-6 ">
      {/* Decorative background waves */}
      <div className="absolute inset-0">
        <div className="absolute bottom-0 left-0 right-0 h-52 bg-gray-500 opacity-30 rounded-t-full blur-3xl"></div>
        <div className="absolute bottom-16 left-1/2 transform -translate-x-1/2 w-[95%] h-44 bg-blue-400 opacity-20 blur-3xl rounded-full"></div>
      </div>

      {/* Floating Card */}
      <motion.div
        className="relative z-10 bg-gray-500 backdrop-blur-md rounded-3xl shadow-8xl py-50 px-40 w-full max-w-5xl text-center border border-gray-400 "
        initial={{ y: 60, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8 }}
      >
        <h1 className="text-5xl md:text-7xl font-extrabold mb-8 text-slate-50">
          Welcome to <span className="text-blue-200">Our Marketplace</span>
        </h1>
        <p className="text-xl md:text-2xl mb-10 text-slate-200">
          Discover amazing products, unbeatable deals, and fast delivery â€” all in one place.
        </p>
        <button className="px-10 py-4 bg-white text-gray-700 font-semibold text-lg rounded-full shadow-lg hover:bg-slate-100 transition">
          Start Shopping
        </button>
      </motion.div>
    </section>
  );
}
