import React from "react";
import { FaSearch, FaShoppingCart, FaBars } from "react-icons/fa";
import { motion } from "framer-motion";

const Navbar = () => {
  return (
    <motion.header
      className="w-full bg-white-to-r from-gray-700 via-black to-gray-900 shadow-lg sticky top-0 z-50"
      initial={{ y: -80, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.6 }}
    >
      <div className="max-w-7xl mx-auto flex items-left justify-between px-auto py-4">
        {/* Logo */}
        <div className="text-2xl font-extrabold text-slate-700 tracking-wide items-left ">
          E-Market Place
        </div>

        {/* Menu */}
        <div className="flex items-center gap-6 text-gray-600">
          <FaSearch className="cursor-pointer hover:text-slate-900 transition" />
          <FaShoppingCart className="cursor-pointer hover:text-blue-400 transition" />
          <button className="px-5 py-2 bg-slate-800 text-white rounded-xl shadow-md hover:bg-blue-700 transition">
            Sign In
          </button>
          <FaBars className="cursor-pointer hover:text-blue-400 transition" />
        </div>
      </div>
    </motion.header>
  );
};

export default Navbar;
