import React from "react";
import { motion } from "framer-motion";

const products = [
  { id: 1, name: "Dell Laptop", price: "$1200", image: "https://via.placeholder.com/250" },
  { id: 2, name: "Smartphone", price: "$800", image: "https://via.placeholder.com/250" },
  { id: 3, name: "Headphones", price: "$200", image: "https://via.placeholder.com/250" },
];

const FeaturedProducts = () => {
  return (
    <section className="py-16 bg-gray-900 text-white">
      <div className="max-w-7xl mx-auto px-6">
        <h2 className="text-3xl font-bold mb-10 text-center">Featured Products</h2>
        <div className="grid md:grid-cols-3 gap-8">
          {products.map((product) => (
            <motion.div
              key={product.id}
              className="bg-gray-800 p-6 rounded-2xl shadow-md hover:shadow-lg hover:scale-105 transition cursor-pointer"
              whileHover={{ y: -8 }}
            >
              <img
                src={product.image}
                alt={product.name}
                className="w-full h-48 object-cover rounded-lg mb-4"
              />
              <h3 className="text-xl font-semibold">{product.name}</h3>
              <p className="text-blue-400 font-bold">{product.price}</p>
              <button className="mt-4 px-5 py-2 bg-blue-600 rounded-lg hover:bg-blue-700 transition">
                Add to Cart
              </button>
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default FeaturedProducts;
